@mixin flexMixin($direction, $justify, $align) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}
@mixin defaultText {
  font-family: $robotoFont;
  font-size: 18px;
  color: $defaultColor;
  line-height: 27px;
}
@mixin textMixin($family, $size, $color, $height) {
  font-family: $family;
  font-size: $size;
  color: $color;
  line-height: $height;
}
