.header {
  @include flexMixin(row, normal, center);
  margin-bottom: 86px;
}

.navigation {
  &__link {
    display: inline-block;
    @include textMixin($robotoFont, 16px, $defaultColor, 24px);
    margin-right: 15px;
    margin-bottom: 10px;
    &:first-child {
      margin-left: 71px;
    }
    &:last-child{
        margin-right: 0;
    }
  }
}


@media (max-width:765px){
  .header{
    flex-direction: column;
    margin-bottom: 50px;
  }
  .logo{
    margin-bottom: 15px;
  }
  .navigation {
    &__link {
      margin-right: 10px;
      &:first-child {
        margin-left: 0px;
      }
    }
  }
}