.footer {
  padding-bottom: 30px;
  &__title {
    @include defaultText;
    font-weight: bold;
    margin-top: 60px;
    margin-bottom: 25px;
  }
}
.slider {
  @include flexMixin(row, normal, center);
  flex-wrap: wrap;
  &__item {
    @include flexMixin(column,normal,normal);
    position: relative;
    overflow: hidden;
    width: 250px;
    height: 200px;
    padding: 30px;
    border-radius: 10px;
    margin-right: 20px;
    margin-bottom: 30px;
    &::after{
        transition: all 0.2s linear;
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background-color: rgba($color: #3b3b3b, $alpha: 0.8);
    }
    &:hover{
        &::after{
            transition: all 0.2s linear;
            background-color: rgba($color: #3b2dff, $alpha: 0.8);
        }
    }
    &:last-child{
        margin-right: 0;
    }
  }
  &__year{
    @include defaultText;
    margin-bottom: 5px;
  }
  &__title{
    @include defaultText;
    margin-bottom: 10px;
    font-weight: bold;
  }
  &__stars{
    margin-top: auto;
  }
  &__rating{
    @include textMixin($robotoFont,13px,$defaultColor,19px);
    margin-right: 15px;
  }
  &__star{
    margin-right: 2px;
  }
  &__img{
    position: absolute;
    z-index: -2;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  &__arrow{
    cursor: pointer;
    width: 32px;
    height: 32px;
    background-color: transparent;
    border: none;
    span{
      display: none;
    }
  }
  &__prev{
    background: url(../img/leftArrow.svg) no-repeat;
    margin-right: 15px;
  }
  &__next{
    background: url(../img/rightArrow.svg) no-repeat;
  }
  &__dots{
    display: none;
  }
}
