.main {
  width: 60%;
  .titles {
    &__sub-title {
      @include textMixin($robotoFont, 40px, $defaultColor, 40px);
      font-weight: 900;
    }
    &__main-title {
      @include textMixin($robotoFont, 64px, $defaultColor, 78px);
      font-weight: 900;
    }
  }
  .information {
    &__text {
      @include defaultText;
      color: #afafaf;
      margin-top: 25px;
      margin-bottom: 25px;
    }
    &__date-text{
        display: block;
        @include defaultText;
        color: #B02000;
    }
    &__date{
        display: block;
        @include textMixin($robotoFont, 24px, $defaultColor, 36px);
        margin-bottom: 46px;
    }
    &__button{
        @include textMixin($robotoFont, 14px, $defaultColor, 16px);
        cursor: pointer;
        position: relative;
        text-transform: uppercase;
        padding: 25px 34px;
        padding-right: 80px;
        background: linear-gradient(90deg, #FB641A 0%, #FC3309 100%);
        border-radius: 300px;
        border: none;
        &::after{
            content: '';
            position: absolute;
            top: 0;
            right: 0;
            background:url(../img/play.svg) $defaultColor no-repeat 14px 14px;
            width: 65px;
            height: 65px;
            border-radius: 50%;
        }
        &::before{
            content: '';
            position: absolute;
            top: -8px;
            right: -8px;
            width: 81px;
            height: 81px;
            border-radius: 50%;
            background: rgba(255, 255, 255, 0.3);
        }
    }
    &__btn{
      animation-duration: 2s;
      animation-iteration-count: infinite;
    }
  }
}

@media (max-width:765px){
  .main{
    width: 100%;
  }
}
@media (max-width:335px){
.main{
  .titles {
    &__sub-title {
     font-size: 30px;
    }
    &__main-title {
    font-size: 55px;
    }
  }
}
}